.app-container {
  background-color: #f4f5f6;
  display: flex;
}

.app-title {
  font-family: "CiscoSansTTLight", sans-serif;
  color: #1b1c1d;
}

.app-content {
  width: 700px;
  margin-top: 33px;
  margin-bottom: 40px;
  line-height: 24px;
  font-size: 16px;
  font-family: "CiscoSansTTLight", sans-serif;
  color: #1b1c1d;
}

.app-btn {
  background-color: white;
  width: 217px;
  height: 56px;
  border: 1px solid #0076d5;
  box-sizing: border-box;
  border-radius: 48px;
  padding: 12px, 32px;
  color: #0076d5;
  font-family: "CiscoSansTT-Medium", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  transition: color background-color border-color 0.1s;
}

.app-btn:hover {
  color: white;
  background-color: #0051af;
  border-color: #0051af;
}

@media (max-width: 319px) {
  body {
    display: none;
  }
}

/* For mobile phones: */
@media (min-width: 320px) {
  .app-container {
    /* width: 320px; */
    width: 100%;
    margin: 48px 0;
    flex-direction: column-reverse;
  }

  .app-sub {
    margin: 26px 16px 0px 16px;
    flex-direction: column;
  }

  .app-title {
    width: 100%;
    line-height: 32px;
    font-size: 24px;
  }

  .app-content {
    width: 100%;
    margin-top: 19px;
    margin-bottom: 25px;
  }

  .app-btn {
    margin-bottom: 48px;
  }

  .appIcon {
    /* margin: 0 16px 0 16px; */
    /* width: calc(100% - 32px) !important; */
    align-self: center;
    width: 100%;
    max-width: 650px;
  }
}

@media all and (min-width: 768px) {
