.bundle-container {
  display: flex;
}

.bundle-sub {
  flex-direction: column;
}

.bundle-title {
  display: flex;
  align-items: flex-end;
  font-family: "CiscoSansTTLight", sans-serif;
  color: #1b1c1d;
}

.bundle-content {
  font-family: "CiscoSansTTLight", sans-serif;
  color: #1b1c1d;
}

.paragraph-one {
  margin-top: 0px;
  margin-bottom: 14px;
  line-height: 24px;
  font-size: 16px;
}

.paragraph-two {
  margin-bottom: 0px;
  line-height: 20px;
  font-size: 14px;
}

.bundle-btn {
  background-color: white;
  width: 138px;
  height: 56px;
  background: #0076d5;
  border: 1px solid #0076d5;
  box-sizing: border-box;
  border-radius: 48px;
  padding: 12px, 32px;
  color: #ffffff;
  font-family: "CiscoSansTT-Medium", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  transition: color background-color border-color 0.1s;
}

.bundle-btn:hover {
  color: white;
  background-color: #0051af;
  border-color: #0051af;
}

@media (max-width: 319px) {
  body {
    display: none;
  }
}

/* For mobile phones: */
@media (min-width: 320px) {
  .bundle-container {
    /* width: 320px; */
    width: 100%;
    margin: 48px 0 0 0;
    padding: 0;
    flex-direction: column;
  }

  .bundleIcon {
    /* margin: 0 16px 0 0; */
    align-self: flex-start;
    width: 100%;
    /* width: calc(100% - 16px) !important; */
    max-width: 650px;
  }

  .bundle-sub {
    /* width: 320px; */
    /* margin-top: 26px;
    padding: 0 16px; */
    margin: 26px 16px 0 16px;
    width: calc(100% - 32px) !important;
  }

  .bundle-title {
    /* width: 288px; */
    margin: 26px 0 19px 0;
    width: 100%;
    line-height: 32px;
    font-size: 24px;
  }

  .bundle-content {
    /* width: 288px; */
