.service-header {
  text-align: center;
  font-family: "CiscoSansTTLight", sans-serif;
  color: #1b1c1d;
}

.content {
  display: flex;
  padding-bottom: 43px;
}

.content::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  border-top: 1px solid #e6e7e8;
  border-bottom: 1px solid #e6e7e8;
}

.eyebrow {
  height: 20px;
  margin-top: 44px;
  font-family: "CiscoSansTT-Medium", sans-serif;
  font-weight: 500;
  font-size: 14px;
  color: #1b1c1d;
}

.subtile {
  margin-top: 20px;
  padding-top: 6px;
  padding-bottom: 7px;
  font-family: "CiscoSansTTLight", sans-serif;
  font-size: 20px;
  line-height: 28px;
  color: #1b1c1d;
}

.paragraph {
  margin-top: 12px;
  padding-top: 5px;
  padding-bottom: 7px;
  /* height: 96px; */
  font-family: "CiscoSansTTLight", sans-serif;
  font-size: 16px;
  line-height: 24px;
  color: #606162;
}

.details-container {
  width: 102px;
  height: 24px;
  margin-top: 5px;
  margin-bottom: 0;
  position: relative;
  display: flex;
  align-self: flex-end;
  flex-direction: row;
  transition: transform 0.3s ease;
  transform: translateX(0px);
}

.details-container::after {
  align-self: flex-end;
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 72%;
  height: 1px;
  background-color: #0076d5;
  transform: scale(0, 1);
}

.details-container:hover::after {
  align-self: flex-end;
  transform: scale(1, 1);
}

.details {
  display: flex;
  width: 82px;
  padding-top: 2px;
  font-style: normal;
  font-family: "CiscoSansTT-Medium", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #0076d5;
  padding-top: 0.5px;
  padding-bottom: 2.5px;
  transition: margin-right padding-top 0.1s;
}

.details:hover {
  margin-right: 2px;
  padding-top: 0px;
}

.icon {
  display: flex;
  margin-top: 6px;
  margin-left: 0px;
  padding-top: 1px;
  width: 5.5px;
  height: 10.98px;
  transition: margin-left 0.1s;
  fill: #0076d5;
}

.icon:hover {
  margin-left: 2px;
}

.btn {
  background-color: white;
  margin-top: 48px;

  width: 145px;
  height: 56px;
  border: 1px solid #0076d5;
  box-sizing: border-box;
  border-radius: 48px;
  padding: 12px, 32px;
  color: #0076d5;
  font-family: "CiscoSansTT-Medium", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  transition: color background-color border-color 0.1s;
}

.btn:hover {
  color: white;
  background-color: #0051af;
  border-color: #0051af;
}

@media (max-width: 319px) {
  body {
    display: none;
  }
}

/* For mobile phones: */
@media (min-width: 320px) {
  .service-container {
    /* width: 320px; */
    width: 100%;
    margin: 0;
    padding: 0;
    border: 0;
  }

  .service-header {
    /* margin-top: 41px;
    margin-left: 16px;
    width: 288px; */
    margin: 41px 16px 0 16px;
    line-height: 32px;
    font-size: 24px;
  }

  .content {
    /* margin-top: 24px;
    margin-left: 16px;
    width: 288px; */
    margin: 24px 16px 0 16px;
    width: calc(100% - 32px) !important;
    /* height: 313px; */
    position: relative;
    flex-direction: column;
  }

  .content::before {
    width: calc(50% - 16px) !important;
    left: calc(25% - 8px) !important;
  }

  .sub-container {
    width: 100% !important;
    /* height: 313px; */
  }

  .eyebrow {
    width: 100% !important;
  }

  .subtile {
    width: 100% !important;
  }

  .paragraph {
    width: 100% !important;
  }

  .divider {
    width: 0px;
  }

  .btn {
    margin-top: 48px;
    margin-bottom: 48px;
    margin-left: calc(50% - 72.5px) !important;
    margin-right: calc(50% - 72.5px) !important;
  }
}

@media all and (min-width: 768px) {
  /* For tablets */
  .service-container {
    width: 100%;
    margin: 0;
    padding: 0;
    border: 0;
  }

  .service-header {
    /* margin-top: 57px;
    margin-left: 80px;
    width: 608px; */
    margin: 57px 80px 0 80px;
    line-height: 40px;
    font-size: 32px;
  }

  .content {
    /* margin-top: 38px;
    margin-left: 80px;
    width: 608px; */
    margin: 38px 80px 0 80px;
    width: calc(100% - 160px) !important;
    border-top: 1px solid #e6e7e8;
    border-bottom: 1px solid #e6e7e8;
    flex-direction: row;
  }

  .content::before {
    width: 0;
  }

  .sub-container {
    width: calc(100% - 12px) !important;
  }

  .eyebrow {
    width: calc(100% - 12px) !important;
  }

  .subtile {
    width: calc(100% - 12px) !important;
  }

  .paragraph {
    width: calc(100% - 12px) !important;
  }

  .divider {
    width: 24px;
  }

  .btn {
    margin-top: 48px;
    margin-bottom: 64px;
    margin-left: calc(50% - 72.5px) !important;
    margin-right: calc(50% - 72.5px) !important;
  }
}

@media all and (min-width: 1440px) {
  /* For desktop */
  .service-container {
    width: 100%;
    margin: 0;
    padding: 0;
    border: 0;
  }

  .service-header {
    /* margin-top: 73px;
    margin-left: 370px;
    width: 700px; */
    margin-top: 73px;
    margin-bottom: 0;
    margin-left: calc(20% - 91.2px) !important;
    margin-right: calc(20% - 91.2px) !important;
    line-height: 40px;
    font-size: 32px;
  }

  .content {
    /* margin-top: 38px;
    margin-left: 370px;
    width: 700px; */
    margin-top: 73px;
    margin-bottom: 0;
    margin-left: calc(20% + 91.2px) !important;
    margin-right: calc(20% + 91.2px) !important;
    width: calc(60% - 182.4px) !important;
    border-top: 1px solid #e6e7e8;
    border-bottom: 1px solid #e6e7e8;
    flex-direction: row;
  }

  .content::before {
    width: 0px;
  }

  .sub-container {
    width: calc(100% - 12px) !important;
  }

  .eyebrow {
    width: calc(100% - 12px) !important;
  }

  .subtile {
    width: calc(100% - 12px) !important;
  }

  .paragraph {
    width: calc(100% - 12px) !important;
  }

  .divider {
    width: 24px;
  }

  .btn {
    margin-top: 48px;
    margin-bottom: 80px;
    margin-left: calc(50% - 72.5px) !important;
    margin-right: calc(50% - 72.5px) !important;
  }
}
