.invest-container {
  display: flex;
}

.invest-header {
  display: flex;
  /* justify-content: center; */
  align-items: center;
  font-family: "CiscoSansTTLight", sans-serif;
  color: #1b1c1d;
}

.divide {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
}

.vertical-line-up {
  box-sizing: border-box;
  border-right: 1px solid #e6e7e8;
}

.vertical-line-bottom {
  box-sizing: border-box;
  border-right: 1px solid #e6e7e8;
}

.policy-title {
  font-family: "CiscoSansTTLight", sans-serif;
  font-size: 20px;
  line-height: 28px;
  color: #1b1c1d;
}

.policy-content {
  font-family: "CiscoSansTTLight", sans-serif;
  font-size: 16px;
  line-height: 24px;
  color: #606162;
}

.policy-option-container {
  display: flex;
  flex-direction: row;
  position: relative;
  transition: transform 0.3s ease;
  transform: translateX(0px);
}

.policy-option-container::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 76px;
  height: 1px;
  background-color: #0076d5;
  transform: scale(0, 1);
}

.policy-option-container:hover::after {
  transform: scale(1, 1);
}

.doc-option-container {
  display: flex;
  flex-direction: row;
  position: relative;
  transition: transform 0.3s ease;
  transform: translateX(0px);
}

.doc-option-container::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 136px;
  height: 1px;
  background-color: #0076d5;
  transform: scale(0, 1);
}

.doc-option-container:hover::after {
  transform: scale(1, 1);
}

.option {
  display: flex;
  padding-right: 11.5px;
  font-style: normal;
  font-family: "CiscoSansTT-Medium", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #0076d5;
  margin-right: 0px;
  padding-top: 0.5px;
  padding-bottom: 2.5px;
  transition: margin-right padding-top 0.1s;
}

.option:hover {
  margin-right: 2px;
  padding-top: 0px;
}

.icon-invest {
  display: flex;
  margin-top: 4.5px;
  margin-left: 0px;
  padding-top: 1px;
  width: 5.5px;
  height: 10.98px;
  transition: margin-left 0.1s;
  fill: #0076d5;
}

.icon-invest:hover {
  margin-left: 2px;
}

